<template>
  <div>
    <b-form @submit.prevent>
      <validation-observer ref="simpleRules">
        <!--  -->
        <b-card
          v-if="showData"
          :title="showData.id ? $t('SalesVoucherModal.editSalesVoucher') : $t('SalesVoucherModal.createSalesVoucher')"
        >
          <b-row>
            <!--  -->
            <b-form-group
              label-for="salesDate"
              class="col-12 col-sm-4"
            >
              <template #label>
                {{ $t('SalesVoucherList.salesDate') }}
                <span class="text-danger">*</span>
              </template>

              <validation-provider
                #default="{ errors }"
                name="salesDate"
                rules="required"
              >
                <flat-pickr
                  id="salesDate"
                  v-model="showData.sales_date"
                  :placeholder="$t('SalesVoucherList.salesDate')"
                  class="form-control"
                />
                <small class="text-danger">{{ errors[0] }}</small>
              </validation-provider>
            </b-form-group>

            <!--  -->
            <b-form-group
              label-for="sales_no"
              class="col-12 col-sm-4"
            >
              <template #label>
                {{ $t('SalesVoucherList.salesNo') }}
                <span class="text-danger"></span>
              </template>

              <b-form-input
                id="salesNo"
                type="text"
                :placeholder="$t('SalesVoucherList.salesNo')"
                disabled
                :value="showData.sales_order_no"
              />
            </b-form-group>

            <!--  -->
            <b-form-group
              label-for="transferNo"
              class="col-12 col-sm-4"
            >
              <template #label>
                {{ $t('SalesVoucherList.transferNo') }}
              </template>

              <TransferSwitcher
                :show-data="showData"
                :open-transfer="['procurement-orders', 'purchase-orders', 'quotation-orders', 'subscriber-orders']"
                @clear-transfer="clearTransfer"
              />
            </b-form-group>

            <!--  -->
            <b-form-group
              label-for="manufacturer"
              class="col-12 col-sm-4"
            >
              <template #label>
                {{ $t('SalesVoucherList.customer') }}
                <span class="text-danger">*</span>
              </template>

              <ManufacturerSwitcher
                :show-data="showData"
                type="customer"
              />
            </b-form-group>

            <!--  -->
            <b-form-group
              label-for="invoice"
              class="col-12 col-sm-4"
            >
              <template #label>
                {{ $t('SalesVoucherList.invoice') }}
              </template>

              <validation-provider
                #default="{ errors }"
                name="invoice"
              >
                <b-form-input
                  id="invoice"
                  v-model="showData.invoice_no"
                  type="text"
                  :placeholder="$t('SalesVoucherList.invoice')"
                />
                <small class="text-danger">{{ errors[0] }}</small>
              </validation-provider>
            </b-form-group>

            <!--  () -->
            <b-form-group
              label-for="subpoena"
              class="col-12 col-sm-4"
            >
              <template #label>
                {{ $t('SalesVoucherList.subpoena') }}
              </template>

              <validation-provider
                #default="{ errors }"
                name="subpoena"
              >
                <b-form-input
                  id="subpoena"
                  v-model="showData.voucher_no"
                  type="text"
                  :placeholder="$t('SalesVoucherList.subpoena')"
                  disabled
                />
                <small class="text-danger">{{ errors[0] }}</small>
              </validation-provider>
            </b-form-group>

            <!--  -->
            <b-form-group
              label-for="businessPeople"
              class="col-12 col-sm-4"
            >
              <template #label>
                {{ $t('SalesVoucherList.businessPeople') }}
                <span class="text-danger">*</span>
              </template>

              <validation-provider
                #default="{ errors }"
                name="businessPeople"
              >
                <StaffSwitcher :show-data="showData" />
                <small class="text-danger">{{ errors[0] }}</small>
              </validation-provider>
            </b-form-group>

            <!--  -->
            <b-form-group
              label-for="salesDepartment"
              class="col-12 col-sm-4"
            >
              <template #label>
                {{ $t('SalesVoucherList.salesDepartment') }}
                <span class="text-danger">*</span>
              </template>

              <validation-provider
                #default="{ errors }"
                name="salesDepartment"
              >
                <v-select
                  id="salesDepartment"
                  label="name"
                  v-model="showData.department_id"
                  :options="salesDepartmentOption"
                  :placeholder="$t('SalesVoucherModal.selectDepartment')"
                  :reduce="option => option.id"
                />
                <small class="text-danger">{{ errors[0] }}</small>
              </validation-provider>
            </b-form-group>

            <!--  -->
            <b-form-group
              label-for="preDeliveryDate"
              class="col-12 col-sm-4"
            >
              <template #label>
                
              </template>

              <validation-provider
                #default="{ errors }"
                name="preDeliveryDate"
              >
                <flat-pickr
                  id="preDeliveryDate"
                  v-model="showData.delivery_date"
                  placeholder=""
                  class="form-control"
                />
                <small class="text-danger">{{ errors[0] }}</small>
              </validation-provider>
            </b-form-group>

            <!--  -->
            <b-form-group
              label-for="project"
              class="col-12 col-sm-4"
            >
              <template #label>
                {{ $t('SalesVoucherList.project') }}
              </template>

              <validation-provider
                #default="{ errors }"
                name="project"
              >
                <v-select
                  id="project"
                  label="name"
                  v-model="showData.project_id"
                  :options="projectOption"
                  :placeholder="$t('SalesVoucherModal.selectProject')"
                  :reduce="option => option.id"
                />
                <small class="text-danger">{{ errors[0] }}</small>
              </validation-provider>
            </b-form-group>

            <!--  -->
            <b-form-group
              label-for="accountSettingMethod"
              class="col-12 col-sm-4"
            >
              <template #label>
                {{ $t('SalesVoucherList.accountSettingMethod') }}
                <span class="text-danger">*</span>
              </template>

              <validation-provider
                #default="{ errors }"
                name="accountSettingMethod"
              >
                <v-select
                  id="accountSettingMethod"
                  label="label"
                  v-model="showData.billing_type"
                  :options="[
                    { label: '', value: 'singleBill' },
                    { label: '', value: 'noBill' },
                    { label: '', value: 'billingAfterReceiptOfInvoice' },
                  ]"
                  :placeholder="$t('SalesVoucherList.selectAccountSettingMethod')"
                  :reduce="option => option.value"
                />
                <small class="text-danger">{{ errors[0] }}</small>
              </validation-provider>
            </b-form-group>

            <!--  -->
            <b-form-group
              label-for="taxDeductionCategory"
              class="col-12 col-sm-4"
            >
              <template #label>
                {{ $t('SalesVoucherList.taxDeductionCategory') }}
                <span class="text-danger">*</span>
              </template>

              <validation-provider
                #default="{ errors }"
                name="taxDeductionCategory"
              >
                <v-select
                  id="taxDeductionCategory"
                  label="label"
                  v-model="showData.tax_type"
                  :options="[
                    { label: '', value: 'taxFree' },
                    { label: '', value: 'taxableIncluded' },
                    { label: '', value: 'taxablePlus' },
                  ]"
                  :placeholder="$t('SalesVoucherList.selectTaxDeductionCategory')"
                  :reduce="option => option.value"
                />
                <small class="text-danger">{{ errors[0] }}</small>
              </validation-provider>
            </b-form-group>

            <!--  -->
            <b-form-group
              label-for="currency"
              class="col-12 col-sm-4"
            >
              <template #label>
                {{ $t('SalesVoucherList.currency') }}
                <span class="text-danger">*</span>
              </template>

              <validation-provider
                #default="{ errors }"
                name="currency"
                rules="required"
              >
                <v-select
                  id="currency"
                  label="name"
                  v-model="showData.currency_id"
                  :options="currencyOption"
                  :placeholder="$t('SalesVoucherModal.selectCurrency')"
                  :reduce="option => option.id"
                />
                <small class="text-danger">{{ errors[0] }}</small>
              </validation-provider>
            </b-form-group>

            <!--  -->
            <b-form-group
              label-for="untaxedAmount"
              class="col-12 col-sm-4"
            >
              <template #label>
                {{ $t('SalesVoucherList.untaxedAmount') }}
              </template>

              <validation-provider
                #default="{ errors }"
                name="untaxedAmount"
              >
                <b-form-input
                  id="untaxedAmount"
                  type="text"
                  :placeholder="$t('SalesVoucherList.untaxedAmount')"
                  disabled
                  :value="getUntaxedAmount()"
                />
                <small class="text-danger">{{ errors[0] }}</small>
              </validation-provider>
            </b-form-group>

            <!--  -->
            <b-form-group
              label-for="tax"
              class="col-12 col-sm-4"
            >
              <template #label>
                {{ $t('SalesVoucherList.tax') }}
              </template>

              <validation-provider
                #default="{ errors }"
                name="tax"
              >
                <b-form-input
                  id="tax"
                  type="text"
                  :placeholder="$t('SalesVoucherList.tax')"
                  disabled
                  :value="getTaxAmount()"
                />
                <small class="text-danger">{{ errors[0] }}</small>
              </validation-provider>
            </b-form-group>

            <!--  -->
            <b-form-group
              label-for="total"
              class="col-12 col-sm-4"
            >
              <template #label>
                {{ $t('SalesVoucherList.total') }}
              </template>

              <validation-provider
                #default="{ errors }"
                name="total"
              >
                <b-form-input
                  id="total"
                  type="text"
                  :placeholder="$t('SalesVoucherList.total')"
                  disabled
                  :value="getTotalAmount()"
                />
                <small class="text-danger">{{ errors[0] }}</small>
              </validation-provider>
            </b-form-group>

            <!--  -->
            <b-form-group
              label-for="remark"
              class="col-12"
            >
              <template #label>
                {{ $t('remark') }}
              </template>

              <validation-provider
                #default="{ errors }"
                name="remark"
              >
                <b-form-textarea
                  id="remark"
                  v-model="showData.remark"
                  type="text"
                  :placeholder="$t('remark')"
                  rows="5"
                />
                <small class="text-danger">{{ errors[0] }}</small>
              </validation-provider>
            </b-form-group>
          </b-row>
        </b-card>

        <b-tabs>
          <b-tab
            title=""
            active
          >
            <ProductItemInfo
              v-if="showData"
              :show-data="showData"
            />
          </b-tab>

          <b-tab
            v-if="$route.params.id"
            title=""
          >
            <TransferOrderInfo
              endpoint="sales-orders/transfers"
              :order-id="$route.params.id"
            />
          </b-tab>
        </b-tabs>

        <!--  -->
        <div class="d-sm-flex align-items-center justify-content-center">
          <b-button
            variant="outline-danger"
            type="button"
            :block="$store.getters['app/currentBreakPoint'] === 'xs'"
            class="mb-1 mb-sm-0 mr-0 mr-sm-1"
            :to="{ name: 'SalesOperation-SalesVoucherList' }"
          >
            <feather-icon
              icon="ArrowLeftIcon"
              class="mr-50"
            />
            {{ $t('back') }}
          </b-button>

          <b-button
            variant="primary"
            type="submit"
            :block="$store.getters['app/currentBreakPoint'] === 'xs'"
            @click.prevent="validationForm"
          >
            <feather-icon
              icon="SaveIcon"
              class="mr-50"
            />
            {{ $t('Submit') }}
          </b-button>
        </div>
      </validation-observer>
    </b-form>

  </div>
</template>

<script>
import { ValidationProvider, ValidationObserver } from 'vee-validate'
import { required } from '@validations'
import {
  BCard, BTabs, BTab, BRow, BCol,
  BForm, BFormGroup, BFormInput, BFormTextarea,
  BTableSimple, BThead, BTbody, BTfoot, BTh, BTr, BTd,
  BButton,
} from "bootstrap-vue"
import { ref } from "@vue/composition-api"
import vSelect from 'vue-select'
import flatPickr from 'vue-flatpickr-component'
import ToastificationContent from '@core/components/toastification/ToastificationContent.vue'
import axios from "@axios"
import TransferSwitcher from '@/layouts/components/order/utils/TransferSwitcher.vue'
import ManufacturerSwitcher from '@/layouts/components/order/utils/ManufacturerSwitcher.vue'
import StaffSwitcher from '@/layouts/components/order/utils/StaffSwitcher.vue'
import ProductItemInfo from '@/layouts/components/order/ProductItemInfo.vue'
import TransferOrderInfo from '@/layouts/components/order/TransferOrderInfo.vue'

export default {
  components: {
    ValidationProvider,
    ValidationObserver,

    BCard,
    BTabs,
    BTab,
    BRow,
    BCol,

    BForm,
    BFormGroup,
    BFormInput,
    BFormTextarea,

    BTableSimple,
    BThead,
    BTbody,
    BTfoot,
    BTh,
    BTr,
    BTd,

    BButton,

    vSelect,
    flatPickr,

    TransferSwitcher,
    ManufacturerSwitcher,
    StaffSwitcher,
    ProductItemInfo,
    TransferOrderInfo,
  },
  setup(_, { root, refs }) {
    const API_PATH = 'sales-orders'

    // Record 
    const blankRecord = {
      sales_date: (new Date()).toLocaleDateString(),
      items: [],
    }

    const showData = ref(null)

    // 
    if (root.$route.name === 'SalesOperation-SalesVoucherCreate') {
      showData.value = JSON.parse(JSON.stringify(blankRecord))
    } else {
      axios.get(`/${API_PATH}/${root.$route.params.id}`)
        .then(response => {
          const data = response.data
          showData.value = JSON.parse(JSON.stringify(data))
        })
    }

    // 
    const validationForm = () => {
      refs.simpleRules.validate()
        .then(success => {
          if (!success) return

          if (showData.value.id) {
            axios
              .put(`/${API_PATH}/${showData.value.id}`, showData.value)
              .then(() => {
                root.$toast({
                  component: ToastificationContent,
                  position: 'top-right',
                  props: {
                    title: `${root.$t('updatedSuccess')}`,
                    icon: 'CoffeeIcon',
                    variant: 'success',
                    text: `${root.$t('Sales Voucher')} ${root.$t('updatedSuccess')}!`,
                  },
                })
                root.$router.push({ name: 'SalesOperation-SalesVoucherList' });
              })
              .catch(error => {
                root.$toast({
                  component: ToastificationContent,
                  position: 'top-right',
                  props: {
                    title: `${root.$t('updatedFailed')}`,
                    icon: 'XIcon',
                    variant: 'danger',
                    text: error.response.data.message,
                  },
                })
              })
          } else {
            axios
              .post(`${API_PATH}`, showData.value)
              .then(() => {
                root.$toast({
                  component: ToastificationContent,
                  position: 'top-right',
                  props: {
                    title: `${root.$t('createdSuccess')}`,
                    icon: 'CoffeeIcon',
                    variant: 'success',
                    text: `${root.$t('Sales Voucher')} ${root.$t('createdSuccess')}!`,
                  },
                })
                root.$router.push({ name: 'SalesOperation-SalesVoucherList' });
              })
              .catch(error => {
                root.$toast({
                  component: ToastificationContent,
                  position: 'top-right',
                  props: {
                    title: `${root.$t('createdFailed')}`,
                    icon: 'XIcon',
                    variant: 'danger',
                    text: error.response.data.message,
                  },
                })
              })
          }
        })
    }

    // 
    const addItem = items => {
      items.push({})
    }

    // 
    const removeItem = (items, index) => {
      root.$swal({
        title: `${root.$t('checkDelete')}`,
        text: `${root.$t('cantRevert')}`,
        icon: 'warning',
        showCancelButton: true,
        confirmButtonText: `${root.$t('yes')}`,
        cancelButtonText: `${root.$t('no')}`,
        customClass: {
          confirmButton: 'btn btn-primary',
          cancelButton: 'btn btn-outline-danger ml-1',
        },
        buttonsStyling: false,
      }).then(result => {
        if (result.value) {
          items.splice(index, 1)
        }
      })
    }

    // 
    const transferNoOption = ref([])

    // 
    const currencyOption = ref([])
    axios.post('/currencies/options')
      .then(response => {
        currencyOption.value = response.data
      })

    // 
    const projectOption = ref([])
    axios.post('/projects/options')
      .then(response => {
        projectOption.value = response.data
      })

    // 
    const salesDepartmentOption = ref([])
    axios.post('/departments/options')
      .then(response => {
        salesDepartmentOption.value = response.data
      })

    // 
    const storehouseOption = ref([])
    axios.post('/storehouses/options')
      .then(response => {
        storehouseOption.value = response.data
      })

    // 
    const productOption = ref([])
    const onSearchProduct = (search, loading) => {
      if (search.length) {
        loading(true);

        axios.post('/products/options', { searchTerm: escape(search) })
          .then(response => {
            productOption.value = response.data
            loading(false)
          })
      }
    }

    // 
    const selectProduct = (select, item) => {
      if (select) {
        root.$set(item, 'product_id', select.id);
        root.$set(item, 'product_code', select.code);
        root.$set(item, 'product_sku', select.sku);
        root.$set(item, 'product_unit', select.unit);
        root.$set(item, 'quantity', 1);
        root.$set(item, 'price', select.price);
      }
    }

    // 
    const getUntaxedAmount = () => {
      if (!showData.value) return 0

      const items = showData.value.items

      if (items.length === 0) return 0

      const total = items.reduce((s, original) => s + (parseFloat(original.quantity || 0) * parseFloat(original.price || 0)), 0)

      switch (showData.value.tax_type) {
        // 
        case 'taxFree':
          return Math.round(total);
        // 
        case 'taxableIncluded':
          return Math.round(total / 1.05);
        // 
        case 'taxablePlus':
          return Math.round(total);
        default:
          return 0
      }
    }

    // 
    const getTaxAmount = () => {
      if (!showData.value) return 0

      const items = showData.value.items

      if (items.length === 0) return 0

      const total = items.reduce((s, original) => s + (parseFloat(original.quantity || 0) * parseFloat(original.price || 0)), 0)

      switch (showData.value.tax_type) {
        // 
        case 'taxFree':
          return 0;
        // 
        case 'taxableIncluded':
          return Math.round(total - getUntaxedAmount());
        // 
        case 'taxablePlus':
          return Math.round(total * 0.05);
        default:
          return 0
      }
    }

    // 
    const getTotalAmount = () => {
      if (!showData.value) return 0

      const items = showData.value.items

      if (items.length === 0) return 0

      const total = items.reduce((s, original) => s + (parseFloat(original.quantity || 0) * parseFloat(original.price || 0)), 0)

      switch (showData.value.tax_type) {
        // 
        case 'taxFree':
          return Math.round(total);
        // 
        case 'taxableIncluded':
          return Math.round(total);
        // 
        case 'taxablePlus':
          return Math.round(total * 1.05);
        default:
          return 0
      }
    }

    // 
    const clearTransfer = () => {
      showData.value = JSON.parse(JSON.stringify(blankRecord))
    }

    return {
      showData,

      validationForm,

      addItem,
      removeItem,

      transferNoOption,
      currencyOption,
      projectOption,
      salesDepartmentOption,
      storehouseOption,
      productOption,

      onSearchProduct,
      selectProduct,

      // 
      getUntaxedAmount,
      getTaxAmount,
      getTotalAmount,

      // 
      required,

      clearTransfer,
    }
  },
}
</script>

<style lang="scss" >
tbody {
  tr {
    td {
      padding: 0.5rem !important;
    }
  }
}

@media (max-width: 767px) {
  .table-responsive .dropdown-menu {
    position: static !important;
  }
}
@media (min-width: 768px) {
  .table-responsive {
    overflow-y: visible;
  }
}

@import "@core/scss/vue/libs/vue-select.scss";
@import "@core/scss/vue/libs/vue-flatpicker.scss";
</style>
